import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Category from '../views/Category.vue'
import Viewer from '../views/viewer.vue'

Vue.use(VueRouter);

const routes = [
	{
		path: '/',
		name: 'Home',
		component: Home
	},
	{
		path: '/category',
		name: 'Category',
		component: Category
	},
	{
		path: '/view',
		name: 'Viewer',
		component: Viewer
	},
	{
		path: '/about',
		name: 'AboutSite',
		component: function () {
			return import('../views/about/site.vue')
		}
	},
	{"path":"/chinesecolor","name":"chinesecolor","component":function(){return import("../pages/chinesecolor.vue")}},{"path":"/freemusic","name":"freemusic","component":function(){return import("../pages/freemusic.vue")}},{"path":"/japanesecolor","name":"japanesecolor","component":function(){return import("../pages/japanesecolor.vue")}},{"path":"/latex","name":"latex","component":function(){return import("../pages/latex.vue")}},{"path":"/markup","name":"markup","component":function(){return import("../pages/markup.vue")}},{"path":"/popsci/quantum1","name":"quantum1","component":function(){return import("../pages/popsci/quantum1.vue")}},{path:'/aboutMe',name:'AboutMe',component:function(){return import('../views/aboutMe.vue')}},
	{
		path: '*',
		name: 'PageNotFound',
		component: function () {
			return import('../views/404.vue')
		}
	}
];

const router = new VueRouter({
	mode: 'hash',
	base: process.env.BASE_URL,
	routes
});
router.afterEach((to, from) => {
	if (to.name === 'Category') {
		let list = getPathNameList(to.query.c.split(','), false);
		if (!list) {
			document.title = Vue.prototype.SiteName;
		}
		else {
			document.title = Vue.prototype.SiteName + ' / ' + list.map(c => c.name).join(' / ');
		}
	}
	else {
		document.title = Vue.prototype.SiteName;
	}
	document.querySelector('#app').scrollTo(0, 0);
});

export default router;